export class ZIconLoader {
  defaultIcon = '';
  defaultOpacity = 1;

  /**
   * 
   * @param iconName
   */
  format (iconName: string): string {
    return iconName;
  }

  /**
   * 
   * @param iconName
   */
  check (iconName: string): boolean {
    return false
  }

  /**
   * 
   * @param vm
   * @param iconName
   * @param fileName
   */
  load (vm: any, iconName: string, fileName: string): Promise<any> {
    return Promise.resolve(iconName);
  }
}
