import Vue from 'vue'
import Zui from '../framework'
import { ZuiGlobalPreset, ZuiUseOptions } from '../../types'

/**
 *  Zui  $vuetify$ui$message$modal
 */
export function createZui (options?: ZuiGlobalPreset, useOptions?: ZuiUseOptions) {
  //  vuetify -> zui
  Vue.use(Zui, useOptions)

  return new Zui({
    ...options,
  })
}
